table,
th,
td {
  border-bottom: 1px solid black;
  border-collapse: collapse;
}
.total-price
{
    margin: 5% 0 0 87%;
}
td {
  text-align: center;
  font-size: 1.5rem;
}
table {
  width: 50vw;
  height: 30vh;
}
.checkout {
  margin: auto;
  width: 50%;
  padding: 10px;
}
.counting {
  width: 35px!important;
  height: 7px!important;
  margin: 0 15px!important;
  background-color: white!important;
}
.payment-btn
{
  width: 25%;
  position: relative;
  bottom: 32px;
}
@media (max-width: 810px) {
  .checkout {
    margin: 0;
    width: 100%;
    padding: 0;
  }
  table {
    width: 95vw;
    height: 30vh;
  }
  .counting {
    width: 31px!important;
    height: 5px!important;
    margin: 0 7px!important;
  }
  th{
    font-size: 1rem; 
}
td {
  text-align: center;
  font-size: 1rem;
}
.total-price
{
    margin:3% 0 0 70%;
}
.quantity
{
  width: 22%;
}
.payment-btn
{
  width: 35%;
  position: relative;
  bottom: 32px;
}
}
