.directory {
  margin: 20px 25px;
}
.item-menu {
  text-align: center;
  background-size: cover;
  height: 13rem;
  background-position: center;
}
.item-menu-lg {
  text-align: center;
  background-size: cover;
  height: 20rem;
  background-position: center;
}
.backgroundImage-before :hover {
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}
.backgroundImage-before {
  height: 100%;
  width: 100%;
  position: relative;
  bottom: 32px;
  cursor: pointer;
}

.content {
  text-align: center;
  border: 1px solid black;
  opacity: 0.7;
  padding-bottom: 18px;
  background-color: #eee;
  border-radius: 5px;
  position: absolute;
  z-index: 1;
}
.content:hover {
  opacity: 0.9;
}

.item-menu-lg .content {
  margin-top: 87px;
  width: 13%;
  margin-left: 17.2%;
  font-size: 0.85rem;
}
.item-menu .content {
  margin-top: 30px;
  width: 13%;
  margin-left: 8%;
  font-size: 0.85rem;
}
@media (max-width: 599px) {
  .item-menu .content {
    width: 31%;
    margin: 48px 25% 0 21%;
    font-size: 0.7rem;
  }

  .item-menu-lg .content {
    margin-top: 95px;
    font-size: 0.7rem;
    width: 30%;
    margin-left: 25%;
    margin: 95px 25% 0 23%;
  }
}
@media (max-width: 835px) and (min-width: 600px) {
  .item-menu .content {
    margin-top: 38px;
    width: 20%;
    margin-left: 2.1%;
    font-size: 0.74rem;
  }

  .item-menu-lg .content {
    margin-top: 90px;
    width: 20%;
    margin-left: 10%;
    font-size: 0.85rem;
  }
}
